import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import IconButton                                   from 'material-ui/IconButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import SwipeableViews                               from 'react-swipeable-views';







/* ################################################### */
/*          helper         */
/* ################################################### */

import { DateUtil }                                 from '../../../../_util/util.date';
import { NumberUtil }                               from '../../../../_util/util.number';
import { ObjectUtil }                               from '../../../../_util/util.object';







/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';

import ComponentCompanyUserStep2Search              from './search/c.company.user.step2.search';
import ComponentCompanyUserStep2Detail              from './detail/c.company.user.step2.detail';




import './s.company.user.step2.css';







export default class ComponentCompanyUserStep2 extends ComponentAbstract {



    static propTypes = {
        form                            : PropTypes.object
        , formValidation                : PropTypes.object
        , data                          : PropTypes.object

        , result                        : PropTypes.object

        , onChangeFormSearch            : PropTypes.func
        , onChangeFormInfo              : PropTypes.func
        , onChangeSlide                 : PropTypes.func

        , onClickAdd                    : PropTypes.func
        , onClickEdit                   : PropTypes.func
        , onClickDelete                 : PropTypes.func
        , onClickSearch                 : PropTypes.func
        , onClickSubmit                 : PropTypes.func
    }


    static defaultProps = {
        form                            : {}
        , formValidation                : {}
        , data                          : {}

        , result                        : {}

        , onChangeFormSearch            : () => {}
        , onChangeFormInfo              : () => {}
        , onChangeSlide                 : () => {}

        , onClickAdd                    : () => {}
        , onClickEdit                   : () => {}
        , onClickDelete                 : () => {}
        , onClickSearch                 : () => {}
        , onClickSubmit                 : () => {}
    }







    constructor( props ) {
        super( props )
    }














    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {

        return (
            <div>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 12 }>

                            <RaisedButton
                                            className={ ' companyUser-step2-marginLeft-20 companyUser-step2-marginRight-20 ' }
                                            label={ "SEARCH" }
                                            secondary={ true }
                                            onClick={ ( evt ) => {
                                                this.props.onChangeSlide( 0 );
                                            } } />

                            <RaisedButton
                                            className={ ' companyUser-step2-marginLeft-20 companyUser-step2-marginRight-20 ' }
                                            label={ "ADD" }
                                            secondary={ true }
                                            onClick={ ( evt ) => {
                                                this.props.onChangeSlide( 1 );
                                            } } />

                        </Col>
                    </Row>
                </Grid>



                {
                    this.props.form.slide.userSlideIndex == 0
                    ?
                        (
                            <div>
                                <ComponentCompanyUserStep2Search

                                                    form={ this.props.form }
                                                    formValidation={ {} }
                                                    data={ this.props.data }

                                                    result={ this.props.result }

                                                    onChangeFormSearch={ () => {
                                                        this.props.onChangeFormSearch();
                                                    } }
                                                    onClickSearch={ () => {
                                                        this.props.onClickSearch();
                                                    } }
                                                    onClickEdit={ ( value ) => {
                                                        this.props.onClickEdit( value );
                                                        this.props.onChangeSlide( 2 );

                                                    } }

                                />
                            </div>
                        )
                    :
                        (
                            <div>
                                <ComponentCompanyUserStep2Detail

                                                    form={ this.props.form }
                                                    formValidation={ {} }
                                                    data={ this.props.data }

                                                    result={ this.props.result }

                                                    onChangeForm={
                                                        ( key , value ) => {
                                                            this.props.onChangeFormInfo( key , value );
                                                        }
                                                    }

                                                    onClickSubmit={ () => {
                                                        this.props.onClickSubmit();
                                                    } }

                                />
                            </div>
                        )
                }

            </div>
        )
    }
}



