        requestInit( ) {
            console.log( 'ComponentCompanyUser : requestInit : ***' )

            this.setState(
                { isLoading: false }
                , () => {

                    HelperVerifyAuthorize.verify()
                        .then(
                            () => {
                                Promise.all( [
                                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                ] ).then(
                                    ( value ) => {
                                        this.dispatch(
                                            ReduxActionCompanyUser.rInit(
                                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                                , { }
                                            )
                                        )
                                    }
                                )
                            }
                        ).catch (
                            ( error ) => {
                                this.requestError( error );
                            }
                        ) ;

                }
            );

        }
        requestInitDone( result ) {
            console.log( 'ComponentCompanyUser : requestInitDone : *** : result => ', result );

            let data = this.state.data;
            data[ 'status' ]            = result.status;
            data[ 'servicePackage' ]    = result.servicePackage;
            this.setState( {
                isLoading   : false
                , data      : data
            } );
            this.dispatch( ReduxActionCompanyUser.rRequestEnd() );
        }


