import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import IconButton                                   from 'material-ui/IconButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';









/* ################################################### */
/*          helper         */
/* ################################################### */

import { DateUtil }                                 from '../../../../_util/util.date';
import { NumberUtil }                               from '../../../../_util/util.number';







/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';






import './s.company.user.step4.css';







export default class ComponentCompanyUserStep3 extends ComponentAbstract {



    static propTypes = {
        form                : PropTypes.object
        , formValidation    : PropTypes.object
        , data              : PropTypes.object

        , result            : PropTypes.object

        , onClickSubmit     : PropTypes.func

    }


    static defaultProps = {
        form                : {}
        , formValidation    : {}
        , data              : {}

        , result            : {}

        , onClickSubmit     : () => {}
    }







    constructor( props ) {
        super( props )

    }











    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {
        return (
            <div>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 2 } > Company ID </Col>

                        <Col md={ 9 }>
                            -
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Company Name </Col>

                        <Col md={ 9 }>
                            -
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Role </Col>

                        <Col md={ 9 }>

                            {
                                ( () => {

                                    let label = '';
                                    for ( let i = 0 ; i < this.props.data.role.length; i ++ ) {
                                        let itemObj = this.props.data.role[ i ];
                                        if ( itemObj[ 'code' ] == this.props.form.user.role ) {
                                            label = itemObj[ 'label' ];
                                        }
                                    }
                                    return label;

                                } )()
                            }

                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > User ID </Col>

                        <Col md={ 9 }>
                            { this.props.form.user.userName }
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > User Name </Col>

                        <Col md={ 9 }>
                            { this.props.form.user.name }
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Id Type </Col>

                        <Col md={ 9 }>
                            { this.props.form.user.idType }
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Id Number </Col>

                        <Col md={ 9 }>
                            { this.props.form.user.idNumber }
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Mobile Number </Col>

                        <Col md={ 9 }>
                            { this.props.form.user.contactNo }
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Email </Col>

                        <Col md={ 9 }>
                            { this.props.form.user.email }
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Transaction Mode </Col>

                        <Col md={ 9 }>
                            {
                                ( () => {

                                    let label = '';
                                    for ( let i = 0 ; i < this.props.data.transactionMode.length; i ++ ) {
                                        let itemObj = this.props.data.transactionMode[ i ];
                                        if ( itemObj[ 'code' ] == this.props.form.user.transactionMode ) {
                                            label = itemObj[ 'label' ];
                                        }
                                    }
                                    return label;

                                } )()
                            }
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Status </Col>

                        <Col md={ 9 }>
                            {
                                ( () => {
                                    let label = '';
                                    for ( let i = 0 ; i < this.props.data.status.length; i ++ ) {
                                        if ( this.props.data.status[ i ].code == this.props.form.user.status ) {
                                            label = this.props.data.status[ i ].label;
                                        }
                                    }
                                    return label;
                                } )()
                            }
                        </Col>
                    </Row>









                    <Row className="show-grid">
                        <Col md={ 12 } className={ ' companyUser-step2-textAlign-center ' } >

                            <RaisedButton
                                            label={ "Submit" }
                                            onClick={ ( event ) => {
                                                this.props.onClickSubmit();
                                            } } />

                        </Col>
                    </Row>



                </Grid>


            </div>
        )
    }
}



