import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import IconButton                                   from 'material-ui/IconButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import SwipeableViews                               from 'react-swipeable-views';







/* ################################################### */
/*          helper         */
/* ################################################### */

import { DateUtil }                                 from '../../../../../_util/util.date';
import { NumberUtil }                               from '../../../../../_util/util.number';
import { ObjectUtil }                               from '../../../../../_util/util.object';







/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../../_abstract/c.abstract';





import '../s.company.user.step2.css';





export default class ComponentCompanyUserStep2Search extends ComponentAbstract {



    static propTypes = {
        form                            : PropTypes.object
        , formValidation                : PropTypes.object
        , data                          : PropTypes.object

        , result                        : PropTypes.object

        , onChangeFormSearch            : PropTypes.func

        , onClickSearch                 : PropTypes.func
        , onClickEdit                   : PropTypes.func
    }


    static defaultProps = {
        form                        : {}
        , formValidation            : {}
        , data                      : {}

        , result                    : {}

        , onChangeFormSearch        : () => {}

        , onClickSearch             : () => {}
        , onClickEdit               : () => {}
    }







    constructor( props ) {
        super( props )

    }











    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {

        return (
            <div className={ ' companyUser-step2-paddingTop-40 ' }>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 2 } > CIF Number </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "CIF Number" }
                                        value={ this.props.form.search.cifNumber }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeFormSearch( 'cifNumber' , newValue );
                                        } } />
                        </Col>
                    </Row>





                    <Row className="show-grid">
                        <Col md={ 12 } className={ ' companyUser-step2-textAlign-center ' } >

                            <RaisedButton
                                            label={ "Search" }
                                            onClick={ ( event ) => {
                                                this.props.onClickSearch();
                                            } } />

                        </Col>
                    </Row>
                </Grid>



                <div className={ ' companyUser-step2-paddingTop-40 ' }>

                    <Table
                                fixedHeader={ false }
                                style={ { flexGrow: '100%', tableLayout: "auto" } }>

                        <TableHeader
                                adjustForCheckbox={ false }
                                displaySelectAll={ false }>

                            <TableRow>
                                <TableHeaderColumn> # </TableHeaderColumn>
                                <TableHeaderColumn> Created Date </TableHeaderColumn>
                                <TableHeaderColumn> User ID </TableHeaderColumn>
                                <TableHeaderColumn> Status </TableHeaderColumn>
                                <TableHeaderColumn> User Name </TableHeaderColumn>
                                <TableHeaderColumn> ID Type </TableHeaderColumn>
                                <TableHeaderColumn> ID Number </TableHeaderColumn>
                                <TableHeaderColumn> Contact No </TableHeaderColumn>
                                <TableHeaderColumn> Email </TableHeaderColumn>
                                <TableHeaderColumn> Role </TableHeaderColumn>
                                <TableHeaderColumn> Transaction Mode </TableHeaderColumn>
                                <TableHeaderColumn> Token </TableHeaderColumn>
                            </TableRow>

                        </TableHeader>

                        <TableBody
                                    displayRowCheckbox={ false }
                                    showRowHover={ true }>

                                {
                                    this.props.result.user.map(
                                        ( item , i ) => {
                                            return (
                                                <TableRow
                                                                key={ '_company_user_add_' + i }
                                                                hoverable={ false }
                                                                selectable={ false } >

                                                    <TableRowColumn>

                                                        <RaisedButton
                                                                        label={ "UPDATE" }
                                                                        secondary={ true }
                                                                        onClick={ ( evt ) => {
                                                                            this.props.onClickEdit( item[ 'userId' ] );
                                                                        } } />

                                                    </TableRowColumn>
                                                    <TableRowColumn> - </TableRowColumn>
                                                    <TableRowColumn> - </TableRowColumn>
                                                    <TableRowColumn> - </TableRowColumn>
                                                    <TableRowColumn> { item[ 'name' ] } </TableRowColumn>
                                                    <TableRowColumn>
                                                        {
                                                            ( () => {
                                                                let label = '';
                                                                for ( let i = 0 ; i < this.props.data.idType.length; i ++ ) {
                                                                    if ( this.props.data.idType[ i ].code == item[ 'idType' ] ) {
                                                                        label = this.props.data.idType[ i ].label;
                                                                    }
                                                                }
                                                                return '';
                                                            } )()
                                                        }
                                                    </TableRowColumn>
                                                    <TableRowColumn> { item[ 'idNumber' ] } </TableRowColumn>
                                                    <TableRowColumn> { item[ 'contactInfo' ][ 0 ][ 'number' ] } </TableRowColumn>
                                                    <TableRowColumn> { item[ 'email' ][ 0 ][ 'emailAddress' ] } </TableRowColumn>
                                                    <TableRowColumn>
                                                        {
                                                            ( () => {
                                                                let label = '';
                                                                for ( let i = 0 ; i < this.props.data.role.length; i ++ ) {
                                                                    if ( this.props.data.role[ i ].code == item[ 'persona' ] ) {
                                                                        label = this.props.data.role[ i ].label;
                                                                    }
                                                                }
                                                                return '';
                                                            } )()
                                                        }
                                                    </TableRowColumn>
                                                    <TableRowColumn>
                                                        {
                                                            ( () => {
                                                                let label = '';
                                                                for ( let i = 0 ; i < this.props.data.transactionMode.length; i ++ ) {
                                                                    if ( this.props.data.transactionMode[ i ].code == item[ 'transactionMode' ] ) {
                                                                        label = this.props.data.transactionMode[ i ].label;
                                                                    }
                                                                }
                                                                return '';
                                                            } )()
                                                        }
                                                    </TableRowColumn>

                                                    <TableRowColumn> { item[ 'token' ][ 0 ][ 'type' ] } - { item[ 'token' ][ 0 ][ 'serialNumber' ] } </TableRowColumn>

                                                </TableRow>
                                            )
                                        }
                                    )
                                }


                        </TableBody>

                    </Table>

                </div>

            </div>
        )
    }
}



