import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */


import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import IconButton                                   from 'material-ui/IconButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import {
    RadioButton
    , RadioButtonGroup
}                                                   from 'material-ui/RadioButton';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import SwipeableViews                               from 'react-swipeable-views';







/* ################################################### */
/*          helper         */
/* ################################################### */

import { DateUtil }                                 from '../../../../../_util/util.date';
import { NumberUtil }                               from '../../../../../_util/util.number';
import { ObjectUtil }                               from '../../../../../_util/util.object';








/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../../_abstract/c.abstract';






import '../s.company.user.step2.css';







export default class ComponentCompanyUserStep2Detail extends ComponentAbstract {



    static propTypes = {
        form                            : PropTypes.object
        , formValidation                : PropTypes.object
        , data                          : PropTypes.object

        , result                        : PropTypes.object

        , onChangeForm                  : PropTypes.func

        , onClickSubmit                 : PropTypes.func
    }


    static defaultProps = {
        form                        : {}
        , formValidation            : {}
        , data                      : {}

        , result                    : {}

        , onChangeForm              : () => {}

        , onClickSubmit             : () => {}
    }







    constructor( props ) {
        super( props )

    }











    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {

        return (
            <div className={ ' companyUser-step2-paddingTop-40 companyUser-step2-paddingBottom-40 ' }>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col md={ 2 } > Company ID </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        value={ '' } />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Company Name </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        value={ '' }  />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Role </Col>

                        <Col md={ 9 }>
                            <SelectField
                                            floatingLabelText={ "SME Type" }
                                            value={ this.props.form.user.role }
                                            onChange={ ( event, value , payload ) => {
                                                this.props.onChangeForm( 'role' , payload );
                                            } }  >
                                {
                                    this.props.data.role.map(
                                        ( item , index ) => {
                                            return (
                                                <MenuItem
                                                            key={ '_company_user_role_' + index }
                                                            value={ item.code }
                                                            primaryText={ item.label } />
                                            );
                                        }
                                    )

                                }
                            </SelectField>
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > User ID </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "User ID" }
                                        value={ this.props.form.user.userName }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeForm( 'userName' , newValue );
                                        } } />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > User Name </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "User Name" }
                                        value={ this.props.form.user.name }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeForm( 'name' , newValue );
                                        } } />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Id Type </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "Id Type" }
                                        value={ this.props.form.user.idType }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeForm( 'idType' , newValue );
                                        } } />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Id Number </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "Id Number" }
                                        value={ this.props.form.user.idNumber }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeForm( 'idNumber' , newValue );
                                        } } />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Mobile Number </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "Mobile Number" }
                                        value={ this.props.form.user.contactNo }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeForm( 'contactNo' , newValue );
                                        } } />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Email </Col>

                        <Col md={ 9 }>
                            <TextField
                                        underlineStyle={ { borderColor: '#616161' } }
                                        underlineFocusStyle={ { borderColor: '#000' } }
                                        hintText={ "Email" }
                                        value={ this.props.form.user.email }
                                        onChange={ ( event , newValue ) => {
                                            this.props.onChangeForm( 'email' , newValue );
                                        } } />
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Transaction Mode </Col>

                        <Col md={ 9 }>
                            <SelectField
                                            floatingLabelText={ "Transaction Mode" }
                                            value={ this.props.form.user.transactionMode }
                                            onChange={ ( event, value , payload ) => {
                                                this.props.onChangeForm( 'transactionMode' , payload );
                                            } }  >
                                {
                                    this.props.data.transactionMode.map(
                                        ( item , index ) => {
                                            return (
                                                <MenuItem
                                                            key={ '_company_user_transactionMode_' + index }
                                                            value={ item.code }
                                                            primaryText={ item.label } />
                                            );
                                        }
                                    )

                                }
                            </SelectField>
                        </Col>
                    </Row>

                    <Row className="show-grid">
                        <Col md={ 2 } > Status </Col>

                        <Col md={ 9 }>
                            {
                                ( () => {
                                    let label = '';
                                    for ( let i = 0 ; i < this.props.data.status.length; i ++ ) {
                                        if ( this.props.data.status[ i ].code == this.props.form.user.status ) {
                                            label = this.props.data.status[ i ].label;
                                        }
                                    }
                                    return label;
                                } )()
                            }
                        </Col>
                    </Row>









                    <Row className="show-grid">
                        <Col md={ 12 } className={ ' companyUser-step2-textAlign-center ' } >

                            <RaisedButton
                                            label={ "Submit" }
                                            onClick={ ( event ) => {
                                                this.props.onClickSubmit();
                                            } } />

                        </Col>
                    </Row>



                </Grid>

            </div>
        )
    }
}



